.program-selection-list {
  width: 280px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  list-style: none;
}

.program-selection-list__item {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 280px;
  margin-bottom: 20px;
  background-color: #f2f2f2;
}

.program-selection-list__item:last-child {
  margin-bottom: 0;
}

.program-selection-list__image-wrapper {
  position: absolute;
  top: 25px;
  width: 50px;
  height: 50px;
  margin-left: 23px;
  background: #68b738;
  border-radius: 50%;
}

.program-selection-list__image {
  position: absolute;
  display: block;

  &--pro-size {
    top: -28px;
    left: -9px;
    width: 67px;
    height: 94px;
  }

  &--slim-size {
    top: -3px;
    width: 51px;
    height: 52px;
  }
}

.program-selection-list__name {
  margin-top: 29px;
  margin-bottom: 31px;
  font-size: 24px;
  line-height: 37px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  text-transform: uppercase;
  color: #111111;

  &--pro-size {
    margin-left: 92px;
  }

  &--slim-size {
    margin-left: 92px;
  }
}

.program-selection-list__promo {
  width: 240px;
  margin: 0 auto;
  padding-bottom: 22px;
  font-size: 14px;
  line-height: 18px;
  font-family: "Arial Regular", "Arial", sans-serif;
  color: #444444;
  border-bottom: 1px solid #d9d9d9;
}

.program-selection-list__toggle {
  text-decoration: none;
  cursor: pointer;
}

.program-selection-list__toggle-name {
  text-align: left;
  width: 240px;
  height: 16px;
  padding: 20px 0 20px 21px;
  font-size: 16px;
  line-height: 16px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
  text-transform: uppercase;
  border: 0;
  background-color: transparent;
  text-decoration: none;
  background-image: url("../img/icon-right-arrow-long.svg");
  background-repeat: no-repeat;
  background-size: 50px 70px;
  background-position-x: 118px;
  background-position-y: 15px;
  cursor: pointer;

  &--pro {
    background-position-x: 116px;
    background-position-y: 15px;
  }

  &:hover {
    background-image: url("../img/icon-right-arrow-verylong.svg");
    background-position-x: 118px;
    background-position-y: 23px;
  }

  &:active {
    color: #b7b7b7;
    background-image: url("../img/icon-right-arrow-verylong-active.svg");
  }
}

@media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
  .program-selection-list {
    width: 708px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .program-selection-list__item {
    width: 708px;
    margin-bottom: 30px;
  }

  .program-selection-list__image-wrapper {
    top: 91px;
    right: 62px;
    width: 200px;
    height: 200px;
    margin-left: 23px;
    background: #68b738;
    border-radius: 50%;

    &--pro {
      top: 92px;
      right: 60px;
    }
  }

  .program-selection-list__image {
    order: 2;
    width: 202px;
    height: 216px;
    margin-top: -210px;
    margin-left: 97px;

    &--pro-size {
      width: 268px;
      height: 283px;
      margin-left: 10px;
      top: 148px;
      left: -46px;
    }

    &--slim-size {
      top: 190px;
      right: -1px;
    }
  }

  .program-selection-list__name {
    order: -1;
    width: 370px;
    margin: 0;
    padding-top: 74px;
    padding-left: 53px;
    padding-bottom: 26px;
    font-size: 36px;
    line-height: 37px;
  }

  .program-selection-list__promo {
    order: 0;
    width: 375px;
    margin: 0;
    padding-left: 52px;
    padding-bottom: 36px;
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;

    &--pro-size {
      width: 344px;
    }
  }

  .program-selection-list__toggle-name {
    order: 1;
    width: 350px;
    height: 20px;
    margin: 0;
    padding-left: 52px;
    padding-bottom: 63px;
    font-size: 20px;
    line-height: 20px;
    background-position-x: 184px;
    background-position-y: 18px;

    &--pro {
      background-position-x: 178px;
      background-position-y: 18px;
    }

    &:hover {
      background-position-x: 183px;
      background-position-y: 25px;
    }
  }

  .program-selection-list__toggle-slim {
    margin-left: 13px;
  }

  .program-selection-list__toggle-pro {
    margin-left: 11px;
  }
}

@media (min-width: $desktop-width) {
  .program-selection-list {
    display: flex;
    width: 1220px;
    padding: 0;
    padding-top: 304px;
  }

  .program-selection-list__item {
    width: 570px;
    margin-right: 80px;
    margin-bottom: 0;
  }

  .program-selection-list__item:last-child {
    margin-right: 0;
  }

  .program-selection-list__image-wrapper {
    top: 47px;
    left: 29px;
    width: 100px;
    height: 100px;
    margin-left: 23px;
    background: #68b738;
    border-radius: 50%;

    &--pro {
      top: 47px;
      left: 28px;
    }
  }

  .program-selection-list__name {
    width: 250px;
    margin: 0;
    padding-bottom: 26px;
    font-size: 36px;

    &--slim-size {
      padding-top: 76px;
      padding-left: 213px;
    }

    &--pro-size {
      padding-top: 76px;
      padding-left: 213px;
    }
  }

  .program-selection-list__promo {
    width: 462px;
    margin: 0;
    padding-left: 53px;
    padding-top: 47px;
    padding-bottom: 11px;
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;
  }

  .program-selection-list__toggle-name {
    height: 20px;
    margin: 0;
    padding-left: 52px;
    padding-bottom: 61px;
    font-size: 20px;
    line-height: 20px;
    background-position-x: 184px;
    background-position-y: 18px;

    &--pro {
      background-position-x: 178px;
      background-position-y: 20px;
    }

    &:hover {
      background-position-x: 183px;
      background-position-y: 25px;
    }
  }

  .program-selection-list__toggle-slim {
    margin-left: 13px;
  }

  .program-selection-list__toggle-pro {
    margin-left: 11px;
  }

  .program-selection-list__image {
    width: 100px;
    height: 105px;
    margin-left: 50px;
    margin-top: 40px;

    &--pro-size {
      width: 134px;
      height: 117px;
      left: 0;
      top: 0;
      margin-left: -17px;
      margin-top: -19px;
    }

    &--slim-size {
      width: 101px;
      height: 111px;
      top: 0;
      left: 0;
      margin-left: -1px;
      margin-top: -12px;
    }
  }
}
