.example {
  min-width: 320px;
  width: 100%;
  margin: 0 auto;
  background-color: #f2f2f2;
}

.example__intro {
  width: 280px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 17px;
}

.example__title {
  width: 280px;
  margin: 0 auto;
  padding-top: 17px;
  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
}

.example__text {
  width: 278px;
  margin: 0;
  padding-top: 6px;
  padding-left: 1px;
  font-size: 14px;
  line-height: 18px;
  font-family: "Arial Regular", "Arial", sans-serif;
  color: #444444;
}

.example__cost {
  margin: 0;
  padding-left: 48px;
  padding-bottom: 34px;
  padding-top: 38px;
  font-size: 14px;
  line-height: 20px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
  text-transform: uppercase;
}

.example__cat {
  width: 320px;
  margin: 0 auto;
  padding-bottom: 21px;
}

.example__image {
  width: 320px;
  height: 241px;
}


.example__before-after-list {
  display: flex;
  margin: 0;
  padding-left: 64px;
  padding-right: 57px;
  padding-top: 11px;
  list-style: none;
}

.example__before-after-list-item {
  position: relative;
  margin-right: 17px;

  &:nth-child(3) {
    margin-right: 0;
  }
}

.example__before-text {
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
  text-transform: uppercase;
}

.example__after-slider {
  width: 84px;
  height: 12px;
  border: 1px solid #d9d9d9;
  border-radius: 50px;
  background-color: #ffffff;
  margin-top: 19px;
}

.example__after-runner {
  position: absolute;
  top: 23px;
  left: 6px;
  width: 35px;
  height: 6px;
  border-radius: 50px;
  background-color: #68b738;
}

.example__after-text {
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
  text-transform: uppercase;
}

@media (min-width: $tablet-width) {
  .example {
    min-width: 768px;
    background-color: #ffffffff;
  }

  .example__intro {
    width: 768px;
    padding-bottom: 235px;
    padding-top: 0;
    padding-left: 0;
    padding-right: 0;
    background-color: #ffffffff;
  }

  .example__title {
    width: 708px;
    margin: 0 auto;
    padding-top: 44px;
    font-size: 60px;
    line-height: 60px;
    border-top: 1px solid #e6e6e6;
    background-color: #ffffffff;
  }

  .example__text {
    width: 696px;
    padding-top: 63px;
    padding-left: 30px;
    padding-right: 37px;
    font-size: 16px;
    line-height: 24px;
  }

  .example__cost {
    width: 174px;
    padding-bottom: 30px;
    padding-top: 63px;
    padding-left: 137px;
    font-size: 20px;
    line-height: 26px;
    text-align: right;
  }

  .example__image {
    width: 686px;
    height: 520px;
    margin-left: 46px;
    margin-top: -171px;
  }

  .example__cat {
    min-width: 768px;
    width: 100%;
    padding-bottom: 49px;
    background-color: #eaeaea;
  }

  .example__cat-wrapper {
    width: 768px;
    margin: 0 auto;
  }

  .example__before-after-list {
    padding-left: 99px;
    padding-right: 96px;
    padding-top: 0;
    margin-top: -6px;
  }

  .example__before-after-list-item {
    margin-right: 22px;

    &:nth-child(3) {
      margin-right: 0;
    }
  }

  .example__before-text {
    font-size: 20px;
    line-height: 20px;
    padding-bottom: 5px;

    &--tablet {
      border-bottom: 1px dashed #bebebe;
    }
  }

  .example__after-text {
    font-size: 20px;
    line-height: 20px;
    padding-bottom: 5px;

    &--tablet {
      border-bottom: 1px dashed #bebebe;
    }
  }

  .example__after-slider {
    position: relative;
    width: 429px;
    height: 7px;
    border: 0;
    border-radius: 50px;
    background-color: #dcdcdc;
    margin-top: 26px;
  }

  .example__after-runner {
    position: absolute;
    top: 14px;
    left: 199px;
    width: 32px;
    height: 32px;
    border: 1px solid #cdcdcd;
    border-radius: 50px;
    background-color: #ffffff;

    &::after {
      content: "";
      position: absolute;
      top: 12px;
      left: 12px;
      width: 8px;
      height: 8px;
      border-radius: 50px;
      background-color: #68b738;
    }
  }
}

@media (min-width: $desktop-width) {
  .example {
    display: flex;
    flex-wrap: wrap;
    min-width: 1300px;
    margin-left: 0;
    background-color: #ffffffff;
  }

  .example__wrapper-desktop {
    display: flex;
    min-width: 1220px;
    width: 100%;
    margin: 0 auto;
    background-color: #f2f2f2;
  }

  .example__center {
    display: flex;
    width: 1300px;
    margin: 0 auto;
  }

  .example__intro {
    width: 500px;
    padding-bottom: 0;
    padding-right: 0;
    background-color: #f2f2f2;
  }

  .example__cat {
    width: 686px;
    padding-bottom: 46px;
    background-color: #f2f2f2;
  }

  .example__title {
    width: 1220px;
    padding-top: 24px;
    padding-bottom: 76px;
    border-top: none;
    background-color: #ffffffff;
  }

  .example__text {
    width: 432px;
    padding-top: 70px;
    padding-left: 32px;
    padding-right: 37px;
  }

  .example__cost {
    width: 353px;
    margin-top: -26px;
    padding-left: 6px;
    padding-bottom: 0;
  }

  .example__cost-price {
    display: inline-block;
    padding-left: 58px;
  }

  .example__image {
    width: 686px;
    height: 520px;
    margin-left: 88px;
    margin-top: -193px;
  }

  .example__before-after-list {
    padding-left: 166px;
    padding-right: 96px;
    padding-top: 0;
    margin-top: 17px;
  }

  .example__before-after-list-item {
    margin-right: 21px;

    &:nth-child(3) {
      margin-right: 0;
    }
  }

  .example__before-text {
    font-size: 20px;
    line-height: 20px;
  }

  .example__after-text {
    font-size: 20px;
    line-height: 20px;
  }
}
