.advantages-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 22px;
  padding: 0;
  background-color: white;
  list-style: none;
}

.advantages-list__item {
  position: relative;
  display: flex;
  margin-bottom: 24px;
}

.advantages-list__description {
  width: 200px;
  margin: 0;
  margin-top: 2px;
  margin-left: 76px;
  font-size: 14px;
  line-height: 18px;
  font-family: "Arial Regular", "Arial", sans-serif;
  color: #444444;
}

.advantages-list__image-wrapper {
  position: absolute;
  top: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
}

.advantages-list__image {
  display: block;

  &--leaf {
    padding-left: 7px;
    padding-top: 6px;
  }

  &--powder {
    padding-left: 6px;
    padding-top: 4px;
  }

  &--dining {
    padding-left: 5px;
    padding-top: 6px;
  }

  &--clock {
    padding-left: 6px;
    padding-top: 5px;
  }
}

.advantages-list__number {
  visibility: hidden;
}

@media (min-width: $tablet-width) {
  .advantages-list {
    flex-direction: row;
    flex-wrap: wrap;
    width: 708px;
    margin: 0;
    padding-bottom: 45px;
  }

  .advantages-list__item {
    flex-direction: column;
    width: 324px;
    margin-right: 60px;
    margin-bottom: 75px;

    &:nth-child(3) {
      margin-bottom: 0;
    }

    &:nth-child(4) {
      margin-bottom: 0;
    }
  }

  .advantages-list__item:nth-child(2),
  .advantages-list__item:nth-child(4) {
    margin-right: 0;
  }

  .advantages-list__description {
    z-index: 2;
    width: 264px;
    margin: 0;
    font-size: 16px;
    line-height: 24px;
    padding-top: 112px;
  }

  .advantages-list__image-wrapper {
    top: 0;
    width: 80px;
    height: 80px;
    background-color: #68b738;
  }

  .advantages-list__image {
    display: block;
    padding-left: 16px;
    padding-top: 16px;
  }

  .advantages-list__number {
    z-index: 1;
    align-self: center;
    visibility: visible;
    margin-top: -224px;
    margin-left: 35px;
    font-size: 281px;
    line-height: 281px;
    font-family: "Oswald Regular", "Arial", sans-serif;
    color: #f2f2f2;

    &--one {
      margin-left: 26px;
    }
  }
}

@media (min-width: $desktop-width) {
  .advantages-list {
    flex-direction: row;
    flex-wrap: wrap;
    width: 1220px;
  }

  .advantages-list__item {
    width: 245px;
    margin-right: 80px;
    margin-bottom: 0;
  }

  .advantages-list__item:nth-child(2) {
    margin-right: 80px;
  }

  .advantages-list__item:last-child {
    margin-right: 0;
  }

  .advantages-list__description {
    width: 245px;
    font-size: 16px;
    line-height: 24px;
  }

  .advantages-list__number {
    &--one {
      margin-left: 105px;
    }

    &--two {
      margin-left: 109px;
    }

    &--three {
      margin-left: 118px;
    }

    &--four {
      margin-left: 118px;
    }
  }
}
