.catalog-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  list-style: none;
}

.catalog-list__item {
  display: flex;
  flex-wrap: wrap;
  padding-left: 20px;
  border: 1px solid #e6e6e6;
  border-bottom: none;

  &--more-item {
    flex-direction: column;
    align-items: center;
    padding-left: 0;
  }
}

.catalog-list__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}

.catalog-list__image {
  display: block;
}

.catalog-list__button {
  margin: 0 auto;
  margin-bottom: 24px;
}

.catalog-list__item-link {
  text-decoration: none;
  cursor: pointer;
}

.catalog-list__title-name {
  width: 91px;
  margin-top: 21px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
  text-transform: uppercase;
}

.catalog-list__title-more {
  margin: 0;
  margin-top: 18px;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  color: #111111;
  text-transform: uppercase;
}

.catalog-list__text-more {
  margin: 0;
  padding: 0;
  margin-top: 8px;
  margin-bottom: 21px;
  font-size: 12px;
  line-height: 16px;
  font-family: "Arial Regular", "Arial", sans-serif;
  color: #444444;
}

.catalog-list__button-more {
  margin: 0 auto;
  margin-bottom: 25px;
  padding: 0;
  padding-left: 93px;
  padding-right: 94px;
  padding-top: 9px;
  padding-bottom: 10px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  background-color: #f2f2f2;
  color: #444444;
  border: none;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    background-color: #ebebeb;
  }

  &:active {
    color: #b9b9b9;
  }
}

.catalog-list__image-more {
  margin-top: 26px;
  height: 31px;
}

@media (min-width: $tablet-width) {
  .catalog-list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 708px;
  }

  .catalog-list__item {
    flex-direction: column;
    align-items: center;
    width: 324px;
    margin-bottom: 144px;
    background-color: #f2f2f2;
    border: none;
    padding-left: 0;
    padding-right: 0;

    &:nth-child(7) {
      margin-bottom: 0;
    }

    &--more-item {
      width: 322px;
      margin-bottom: 0;
      background-color: #ffffff;
    }
  }

  .catalog-list__image-more {
    width: 55px;
    height: 56px;
    margin-top: 55px;
  }

  .catalog-list__title-more {
    text-align: center;
    width: 135px;
    margin-top: 47px;
    font-size: 20px;
  }

  .catalog-list__text-more {
    text-align: center;
    width: 168px;
    margin-top: 27px;
    margin-bottom: 89px;
    font-size: 16px;
    line-height: auto;
  }

  .catalog-list__item:last-child {
    border: 1px solid #e6e6e6;
  }

  .catalog-list__item:nth-child(2n) {
    margin-right: 0;
  }

  .catalog-list__image {
    margin-top: -47px;
    margin-left: 0;

    &--small-size {
      width: 318px;
      height: 172px;
    }

    &--big-size {
      width: 319px;
      height: 208px;
      margin-top: -83px;
    }
  }

  .catalog-list__title-name {
    text-align: center;
    width: 130px;
    margin-top: 37px;
    font-size: 20px;
  }

  .catalog-list__button {
    width: 249px;
    margin-bottom: 40px;
  }

  .catalog-list__button-more {
    margin-bottom: 20px;
    padding-top: 10px;
    padding-left: 80px;
    padding-right: 78px;
  }
}

@media (min-width: $desktop-width) {
  .catalog-list {
    flex-direction: row;
    flex-wrap: wrap;
    width: 1220px;
  }

  .catalog-list__item {
    width: 245px;
    margin-top: 0;

    &:nth-child(6),
    &:nth-child(5) {
      margin-bottom: 0;
    }

    &--more-item {
      width: 242px;
      background-color: #ffffff;
    }
  }

  .catalog-list__button-more {
    padding-left: 38px;
    padding-right: 42px;
  }
}
